body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.row{
    color:white;
    margin-left:20px;
}
.row_posters{
    display:flex;
    overflow-y:hidden;
    overflow-x:scroll;
    padding:20px;
}

.row_posters::-webkit-scrollbar{
    display:none;
}

.row_poster{
    object-fit:contain;
    width:100%;
    max-height:100px;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
    margin-right:10px;
}



.row_poster:hover{
    -webkit-transform:scale(1.08);
            transform:scale(1.08);
}

.row_posterLarge{
    max-height:250px;
}

.row_posterLarge:hover{
    -webkit-transform:scale(1.09);
            transform:scale(1.09);
    opacity:1;
}
*{
  margin:0;
}

.app{
  background-color:#111;
  
}
.banner{
    color:white;
    object-fit:contain;
    height:448px;
}

.banner_contents{
    margin-left:30px;
    padding-top:140px;
    height:190px;
}

.banner_title{
    font-size:3rem;
    font-weight:800;
    padding-bottom:0.3rem;
}

.banner_description{
    width:45rem;
    line-height:1.3;
    padding-top:1rem;
    font-size:0.8rem;
    max-width:550px;
    height:80px;
}

.banner_button{
    cursor:pointer;
    color:#fff;
    outline:none;
    border:none;
    font-weight:700;
    border-radius:0.2vw;
    padding-left:2rem;
    padding-right:2rem;
    margin-right:2rem;
    padding-top:0.5rem;
    background-color:rgba(51,51,51,0.5);
    padding-bottom:0.5rem;
}


.banner_button:hover{
    color:#000;
    background-color:#e6e6e6;
    transition: all 0.2s;
}


.banner--fadeBottom{
    height:7.4rem;
    background-image:linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.61),
        #111
    );
}
.nav{
    position:fixed;
    top:0;
    width:100%;
    padding:20px;
    height:30px;
    z-index:1;

    display:flex;
    justify-content:space-between;
    /*Animation for navbar hiding*/
    transition-timing-function:ease-in;
    transition: all 0.5s;
}


.nav_black{
    background-color:black;
}


.nav_logo{
    position:fixed;
    left:20px;
    width:80px;
    object-fit:contain;
}

.nav_avatar{
    position:fixed;
    right:20px;
    width:30px;
    object-fit:contain;
}
